<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_CAMPAIGN"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="campaign_id"
            :HeaderText="$t('Campaign')" :listType="['List']" :ListData="CampaignData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
        </tg-list>
    </section>
</template>

<script>
import Schema from '../SocialMediaCampaign/SocialMediaSchema'
import DataService from '../../../../services/DataService';
export default{
    data(){
        return{
            isLoading:false,
            FaceBookPages:[],
            CampaignData:[],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecords:0,
            SortExp:'',
            SortBy:'',
            PageNumber: 1,
            PageSize: 10,
            searchCondition: '',
            Headers:[
                    {
                        "COLUMN_NAME": "social_media_post",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Social Media Post",
                        "DISPLAY_ORDER": 1
                    },
                    {
                        "COLUMN_NAME": "created_at",
                        "DATA_TYPE": "date",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Created at",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "schedule_on",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Schedule On",
                        "DISPLAY_ORDER": 3
                    },
                    {
                        "COLUMN_NAME": "rejected_by",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Rejected By",
                        "DISPLAY_ORDER": 4
                    },
                    {
                        "COLUMN_NAME": "reason",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Reason",
                        "DISPLAY_ORDER": 5
                    },
                    {
                        "COLUMN_NAME": "reviewed_by",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Reviewed By",
                        "DISPLAY_ORDER": 6
                    },
                    {
                        "COLUMN_NAME": "status_id",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Status",
                        "DISPLAY_ORDER": 7
                    }
                ]
        }
    },
   async created(){
        var vm=this;
        debugger;
        var code =vm.$route.query.code;
        if(code){
           await vm.FacebookAuth(code);
           //await vm.InstagramAuth(code);
        }
        this.CampaignData=[{
            "rownumber": 1,
            "total_records": 125,
            "campaign_id": 572,
            "social_media_post": "Noise watch",
            "reviewed_by": "Dave Junior",
            "created_at": "2023-12-20T19:33:43.160",
            "schedule_on": "2023-12-20T19:33:43.160",
            "reason": "Noise watch",
            "created_by": "James OSmith",
            "rejected_by": "James OSmith",
            "status_id": "Active",
    }]
    vm.FetchData();
    },
    methods:{
        FetchData(){
            //var facebookUserId = this.getLoginData();
        },
        pagerMethod: function (value) {             
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
                if (s === this.SortBy) {                    
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }               
            this.FetchData();
            $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) { 
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                    case "openPopUp":
                        //this.openPopUp();
                    break;
                    case "setTourGuideSteps":
                        //this.setTourGuideSteps(0,this);
                    break;
            }
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event,isChild) {            
            switch (callbackfunction) {
                case 'openQuickViewpopup':
                    //this.DeleteIncidentRow(item.ID);
                    break;
                case 'DeleteCampaign':
                    //this.openReportIncidentPopUp(item.ID,isChild);
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        async FacebookAuth(code){
            var vm=this;
            var url =`code=${code}`
           await DataService.FacebookAuth(url).then(function(response){
                if(response.data){
                    var result = response.data;
                     vm.storeLoginData(result);
                }
            });
        },
        async InstagramAuth(code){
            var vm=this;
            var url =`code=${code}`
           await DataService.InstagramAuth(url).then(function(response){
                if(response.data){
                    var result = response.data;
                     vm.storeLoginData(result);
                }
            });
        },
       storeLoginData(userId) {
            const now = new Date();
            const expires = new Date(now.getTime() + 60 * 24 * 60 * 60 * 1000);
            const data = {
                userId: userId,
                expires: expires.getTime() 
            };
            localStorage.setItem('facebook_login_data', JSON.stringify(data));
        },
    }
}
</script>